/*基础样式*/
.ymt-input {
  	width: 37.5rem;
  	height: 5rem;    

	font-size: 1.7rem;
	color: #fff; 
	background-color: #fff;	
 
	span { 
		width: 13.5rem;
		height: 5rem;
		line-height: 5rem;
		padding-left: 1.2rem;

		color: #2d2d2d;
      font-size: 1.6rem;
	}

	input {
		float: right;
		width: 21.5rem;
		height: 5rem;
      padding-right: 1.2rem;
		border:none; 

		font-size: 1.6rem;
      color: #2d2d2d;

		&:focus {
			outline:none;	   		
		};

      &:disabled {
         background-color: #fff;  
      }; 

      
	}

}

.text-right {
   input {
      text-align: right;   
   }
}

.text-left {
   input {
      text-align: left;
   }
}





.ymt-input-default {
	background-color: #ff9600;
	border-color:  #ff9600;
} 


