/*基础样式*/
.ymt-btn {
  width: 35rem;
  height: 4.4rem;
  border-radius: 0.5rem;
  border-style: none;
  font-size: 1.7rem;
  color: #fff;
}
.ymt-btn-default {
  background-color: #ff9600;
  border-color: #ff9600;
}
/*基础样式*/
.ymt-input {
  width: 37.5rem;
  height: 5rem;
  font-size: 1.7rem;
  color: #fff;
  background-color: #fff;
}
.ymt-input span {
  width: 13.5rem;
  height: 5rem;
  line-height: 5rem;
  padding-left: 1.2rem;
  color: #2d2d2d;
  font-size: 1.6rem;
}
.ymt-input input {
  float: right;
  width: 21.5rem;
  height: 5rem;
  padding-right: 1.2rem;
  border: none;
  font-size: 1.6rem;
  color: #2d2d2d;
}
.ymt-input input:focus {
  outline: none;
}
.ymt-input input:disabled {
  background-color: #fff;
}
.text-right input {
  text-align: right;
}
.text-left input {
  text-align: left;
}
.ymt-input-default {
  background-color: #ff9600;
  border-color: #ff9600;
}
/*基础样式*/
.ymt-bar-nav {
  position: relative;
  top: 0;
  width: 37.5rem;
  height: 4.4rem;
  background-color: #181d23;
  font-size: 1.7rem;
  color: #fff;
}
.ymt-bar-nav .left {
  position: absolute;
  top: 0;
  left: 1.2rem;
  width: 3rem;
  height: 4.4rem;
  line-height: 4.4rem;
  font-size: 1.4rem;
  color: #fff;
  z-index: 1;
}
.ymt-bar-nav .main {
  position: relative;
  width: 100%;
  height: 4.4rem;
  line-height: 4.4rem;
  text-align: center;
  z-index: 0;
}
.ymt-bar-nav .right {
  position: absolute;
  top: 0;
  right: 1.2rem;
  height: 4.4rem;
  line-height: 4.4rem;
  font-size: 1.4rem;
  color: #ff7800;
  z-index: 1;
}
.ymt-list {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
}
.ymt-list .list-group {
  width: 100%;
}
.ymt-list .list-group .group-title {
  padding: 0 1.2rem;
  height: 3.6rem;
  line-height: 3.6rem;
  background-color: #f5f5f5;
  color: #626262;
  font-size: 1.3rem;
}
.ymt-list .list-group .group-title.center {
  text-align: center;
}
.ymt-list .list-group .group-title.right {
  text-align: right;
}
.ymt-list .list-group .list-item {
  width: 100%;
  border-bottom: 0.05rem solid #e0e0e0;
}
.ymt-list .list-space {
  height: 1rem;
}
.ymt-calendar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: auto;
  /*.ymt-calendar-footer {
		position: fixed;
		bottom: 0;
		width: 100%;
		text-align: center;
		background-color: #fff;
	}*/
}
.ymt-calendar .ymt-calendar-week {
  margin: 0 auto;
  width: 100%;
  height: 3.3rem;
  margin: 0 0 1.2rem 0;
  line-height: 3.3rem;
  font-size: 1.2rem;
  color: #626262;
  background-color: #fff;
}
.ymt-calendar .ymt-calendar-week .week-item {
  float: left;
  width: 5.35rem;
  height: 3.3rem;
  line-height: 3.3rem;
  margin: 0;
  padding: 0;
  text-align: center;
  /*border: 1px solid red;*/
}
.ymt-calendar .ymt-calendar-content {
  height: 90%;
  overflow-x: scroll;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body {
  width: 100%;
  background-color: #f0f0f0;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body::after {
  display: block;
  clear: both;
  content: "";
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body.hide {
  visibility: hidden;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-moth {
  position: relative;
  width: 100%;
  padding: 1.6rem 0;
  text-align: center;
  font-size: 1.2rem;
  color: #2d2d2d;
  background-color: #f0f0f0;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day {
  position: relative;
  float: left;
  width: 5.1rem;
  height: 5.1rem;
  line-height: 5.1rem;
  margin: 0.125rem;
  /*border: 1px solid red;*/
  font-size: 1.5rem;
  color: #2d2d2d;
  text-align: center;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day .ymt-calendar-number {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 3.3rem;
  height: 3.3rem;
  line-height: 3.3rem;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.before-status {
  color: #ccc;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.before-status-drive {
  background-color: #f5f5f5;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.before-status-drive .icon-drive {
  position: absolute;
  top: 0;
  right: 0;
  width: 2.1rem;
  height: 2.1rem;
  line-height: 2.1rem;
  background: url('calendar/images/icon_gary@2x.png') no-repeat;
  background-size: 2.1rem 2.1rem;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.today-status-drive {
  background-color: #e9e9e9;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.today-status-drive .icon-drive {
  position: absolute;
  top: 0;
  right: 0;
  width: 2.1rem;
  height: 2.1rem;
  line-height: 2.1rem;
  background: url('calendar/images/icon_gary@2x.png') no-repeat;
  background-size: 2.1rem 2.1rem;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.after-status-drive {
  background-color: #fff;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.after-status-drive .icon-drive {
  position: absolute;
  top: 0;
  right: 0;
  width: 2.1rem;
  height: 2.1rem;
  line-height: 2.1rem;
  background: url('calendar/images/icon_bule@2x.png') no-repeat;
  background-size: 2.1rem 2.1rem;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.cancel {
  background-color: #fff;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.cancel .icon-cancel {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1.5rem;
  line-height: 1.5rem;
  background-color: #fe9600;
  color: #fff;
  font-size: 1.1rem;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.actived .ymt-calendar-number {
  background-color: #6ac1f7;
  border-radius: 50%;
}
.ymt-calendar .ymt-calendar-content .ymt-calendar-body .ymt-calendar-day.actived .icon-selecting {
  position: absolute;
  top: 0;
  right: 0;
  width: 1.75rem;
  height: 1.75rem;
  line-height: 1.75rem;
  border-radius: 50%;
  background-color: #6ac1f7;
  font-size: 1.1rem;
}



